
.caption {
  text-align: center;
  height: 100vh;
  display: flex;
  place-content: center;
  align-items: center;
  background: url("https://media.istockphoto.com/photos/gold-fireworks-border-in-night-sky-picture-id872660122?k=20&m=872660122&s=612x612&w=0&h=Q4nwzYhLv_oS9lWBxQb-buRLThf9OdQesH0t93WhhjI=") no-repeat;
  background-size: cover;
  color: rgb(0, 0, 0)02);
}
.card {
  backdrop-filter: blur(8px) saturate(180%);
  -webkit-backdrop-filter: blur(8px) saturate(180%);
  background-color: rgba(255, 255, 255, 0.39);
  border-radius: 12px;
  border: 1px solid rgba(209, 213, 219, 0.3);
}
